.grid_container {
    position: relative;
    width: 600px;
    height: 600px;
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
}

.grid_container:hover .floating_button {
    opacity: 1;
    pointer-events: auto;
}

.floating_button {
    position: absolute;
    z-index: 999;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.2s ease-in-out;
}

.floating_button_bubble {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3.5rem;
    height: 3.5rem;
    border-radius: 50%;
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0 10px 30px 5px rgba(255, 255, 255, 0.75);
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.25) 0%,rgba(255,255,255,0) 70%);
    transition: background-color 0.3s ease-in-out, box-shadow 2s ease-in-out;
}

.floating_button:hover .floating_button_bubble {
    background-color: rgba(225, 239, 173, 0.9);
    transform: scale(1.05);
}

.ripple_stay {
    animation: ripple_stay 1s 1;
    animation-fill-mode: forwards;
}

.ripple_out {
    animation: ripple_out 1s 1;
    animation-fill-mode: forwards;
}

.floating_button_icon {
    color: black;
    font-size: 1.5rem;
}


.north_button {
    top: 0;
    left: calc(50% - 25px);
}

.south_button {
    bottom: 0;
    left: calc(50% - 25px);
}

.west_button {
    top: calc(50% - 25px);
    left: 0;
}

.east_button {
    top: calc(50% - 25px);
    right: 0;
}

.data_grid {
    width:100%;
    height:100%;
    display: grid;
    grid-template-rows: repeat(9, 1fr);
    gap: 5px;
    background-color: rgba(0, 0, 0, 0.25);
    transition: transform 0.5s ease;
}

.square_row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5px;

    width: 100%;
    height: 100%;
}

.square_row.animate_out_north {
    transition-property: transform;
    transition-duration: 0.5s;
    transition-timing-function: ease-out;
}

.square {
    width: 100%;
    height: 100%;
}


.animate_in_south {
    animation: slideInFromBottom 1s ease-in-out forwards;
}

.animate_out_north {
    animation: slideOutToTop 1s ease-in-out forwards;
}

.animate_up {
    animation: slideUp 1s ease-in-out forwards;
}

.animate_down {
    animation: slideDown 1s ease-in-out forwards;
}


@keyframes slideUp {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-100%);
    }
}

@keyframes slideDown {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(0%);
    }
}


@keyframes slideInFromBottom {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes slideOutToTop {
    0% {
        transform: translateY(0);
        opacity: 1;
    }
    100% {
        transform: translateY(-100%);
        opacity: 0;
    }
}


@keyframes pop {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes ripple_stay {
    0% {
        transform: scale(1.0);
        opacity: 1;
    }
    50% {
        transform: scale(1.1);
        opacity: 0;

    }
    100% {
        transform: scale(1.0);
        opacity: 1;
    }
}


@keyframes ripple_out {
    0% {
        transform: scale(1.0);
        opacity: 1;
    }
   100% {
       transform: scale(1.1);
       opacity: 0;

    }
}

@media all and (max-width: 600px) {
    .grid_container {
        height: 350px!important;
        width: 350px!important;
    }

    .floating_button_bubble {
        width: 2rem;
        height: 2rem;
    }

    .north_button {
        left: calc(50% - 16px);
    }

    .south_button {
        left: calc(50% - 16px);
    }

    .west_button {
        top: calc(50% - 16px);
    }

    .east_button {
        top: calc(50% - 16px);
    }


}

