.header_container {
    z-index: 2;
    position: fixed;;
    height: 100px;
    line-height: 50px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    font-family: boogaloo;
    font-size:40px;
}

.left_header {
    z-index: 2;
    position: absolute;
    left: 20px;
    width: 50px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.right_header {
    position: absolute;
    right: 20px;
    width: 50px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.ape_icon {
    position: relative;
    height: 80px!important;
    width: 80px!important;
    filter: drop-shadow(2px 2px 1px rgba(255, 255, 255, 0.25));
}

.ape_icon svg path{
    fill: rgb(54,54,63);
    opacity: 0.9;
}

.ape_icon svg ellipse{
    fill: rgb(54,54,63);
    opacity: 0.9;
}

.menu_label {
   margin-left: 20px;
}

.view_icon {
    position: relative;
    height: 40px!important;
    width: 40px!important;
    color: rgb(54,54,63);
    opacity: 0.9;
    filter: drop-shadow(2px 2px 1px rgba(255, 255, 255, 0.25));

}


.center_header {
    position: absolute;
    filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.25))
}

@media all and (max-width: 600px) {
    .header_container {
        height: 60px;
        line-height: 60px;
        font-size: 20px;
    }
    .left_header {
        left: 5px;
        width: 60px;
    }
    .ape_icon {
        height: 40px!important;;
        width: 40px!important;;
    }

    .view_icon {
        height: 25px!important;;
        width: 25px!important;;
    }

    .right_header {
        right: 5px;
        width: 60px;
    }
}

